import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable } from 'rxjs';

import { catchError, switchMap, filter, take } from 'rxjs/operators'
import { pipe, throwError, BehaviorSubject } from 'rxjs';


import { UserAuthService } from '../services/auth.service';
import { HOST_API } from '../app.config';
import { convertObjectsToCSV } from '@covalent/core/common';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  private loginurl = HOST_API + 'sid-token/';  // URL to web API
  private refreshurl = HOST_API + 'token/refresh/';  // URL to web API

  constructor(
    private userAuthService: UserAuthService
  ) { }

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    //    return next.handle(request);

    // add auth header with jwt if user is logged in and request is to api url
    // console.log(request)
    //console.log('inside interceptor');

    if (this.userAuthService.isLoggedIn()
      && !request.url.endsWith("/sid-token/")
      && !request.url.endsWith("/token/")
    ) {
      request = request.clone({
        setHeaders: {
          Authorization: `Bearer ${this.userAuthService.token}`
        }
      });
    }

    //console.log('getting there...');
    return next.handle(request).pipe(catchError(error => {

      if (error instanceof HttpErrorResponse && (error.status === 401 || error.status === 403)) {
        if (request.url.endsWith("/token/")) {
          // We do another check to see if refresh token failed
          // In this case we want to logout user and to redirect it to login page
          //console.log('on your way out');

          this.userAuthService.logout();
          return throwError(error);
        }
        else {
          return this.handleError(request, next);
        }
      }
      else {
        return throwError(error);
      }
    }
    ));
    // return next.handle(request);

  }
  private isRefreshing = false;
  private refreshTokenSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  private handleError(request: HttpRequest<any>, next: HttpHandler) {
    if (!this.isRefreshing) {
      this.isRefreshing = true;
      this.refreshTokenSubject.next(null);

      return this.userAuthService.refreshToken().pipe(
        switchMap((token: any) => {
          this.isRefreshing = false;
          this.refreshTokenSubject.next(token.jwt);

          request = request.clone({
            setHeaders: {
              Authorization: `Bearer ${this.userAuthService.token}`
            }
          })

          return next.handle(request);

        })

      );

    } else {
      return this.refreshTokenSubject.pipe(
        filter(token => token != null),
        take(1),
        switchMap(() => {
          request = request.clone({
            setHeaders: {
              Authorization: `Bearer ${this.userAuthService.token}`
            }
          });
          return next.handle(request);
        }));
    }
  }


}
