import { Component, OnInit } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';

import { Router, ActivatedRoute } from '@angular/router';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms'; // new add
import { FormControl } from '@angular/forms';

import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { TdLoadingService } from '@covalent/core/loading';

import { ConnectionsService } from '../../services/connections.service';
import { IConnectionRecords } from '../../models/connection';
import { IConnectors, IDimConnectors } from '../../models/connection';

import { SalesforceComponent } from '../salesforce/salesforce.component';

@Component({
  selector: 'app-searchconnections',
  templateUrl: './searchconnections.component.html',
  styleUrls: ['./searchconnections.component.css']
})
export class SearchConnectionsComponent implements OnInit {

  connectionRecords: IConnectionRecords;
  datarecords: Array<IConnectors>;
  rowSelected = false;

  config = new MatSnackBarConfig();

  dimConnections: Array<IDimConnectors>;
  dataloaded = false;
  allowForm = false;
  errorMessage: string;

  constructor(
    private _loadingService: TdLoadingService,
    private router: Router,
    public _matSnackBar: MatSnackBar,
    private connectionsService: ConnectionsService
  ) {
    this.getConnections();
  }

  ngOnInit(): void {
  }

  getConnections() {
    this._loadingService.register('loadingsid');

    this.connectionsService.getConnections().subscribe(
      result => {
       console.log(result);
        this._loadingService.resolve('loadingsid')
        this.connectionRecords = result;
        this.datarecords = this.connectionRecords.records.connectors;
        this.dataloaded = true;
      },
      err => {
        console.log(err);
        this._loadingService.resolve('loadingsid');
        let msg = 'System Error: please look at console log';
        this._matSnackBar.open(msg, 'Dismiss', this.config);

      })
  }


  newConn() {
    this.router.navigate(['newconnection']);
  }

  editConn(conn){
    this.rowSelected = true;
  }
}
